.message {
    display: flex;
    justify-content: flex-end;
    padding: 20px 0;
}

.imageAndText {
    display: flex;
}

.avatarAndTime {
    display: flex;
    flex-direction: column;
    margin: 10px 0 0 10px;
}
.avatarAndTime img {
    width: 48px;
    height: 48px;
}

.text {
    background-color: #0066CC;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 10px 10px 0 10px;
    padding: 7px 13px;
    max-width: 530px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
    align-self: flex-start;
    position: relative;
}

.text:after {
    content: "";
    position: absolute;
    right: -10px;
    bottom: 0;
    border: 5px solid transparent;
    border-left: 5px solid #0066CC;
    border-bottom: 4px solid #0066CC;
    display: block;
    width: 0;
    height: 0;
}

.name {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    text-align: right;
}

.messageText {
    font-family: 'Montserrat';
    white-space: normal;


}

.time {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
    text-align: center;
    margin-top: 5px;
}
