.sendForm {
    display: flex;
    align-items: center;
}

.textarea {
    width: 100%;
    height: 40px !important;
    background: #F5F7FB;
    box-shadow: 0px 5px 20px rgba(29, 33, 38, 0.03), 0px 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 20px;
    padding: 10px 20px;
    border: transparent;
    outline: transparent;
    resize: none;
}
.textarea::placeholder{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);
    opacity: 0.9;

}

.button {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 20px;
    padding: 5px 28px;
    color: #FFFFFF;
    background: #0066CC;
    border-radius: 20px;
    outline: transparent;
    border: transparent;
    margin-left: 44px;
}

