.friendMessage {
    display: flex;
    justify-content: flex-start;
    padding: 20px 0;
}

.friendImageAndText {
    display: flex;
    flex-direction: row-reverse;

}

.friendAvatarAndTime {
    display: flex;
    flex-direction: column;
    margin: 10px 10px 0 0;
}

.friendAvatarAndTime img {
    width: 48px;
    height: 48px;
}

.friendText:after {
    content: "";
    position: absolute;
    left: -10px;
    bottom: 0;
    border: 5px solid transparent;
    border-right: 5px solid #F5F7FB;
    border-bottom: 5px solid #F5F7FB;
    display: block;
    width: 0;
    height: 0;
}
.friendText{
    background-color: #F5F7FB;
    box-shadow: 0 5px 20px rgba(29, 33, 38, 0.03), 0 1px 2px rgba(29, 33, 38, 0.1);
    border-radius: 10px 10px 10px 0px;
    padding: 7px 13px;
    max-width: 530px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #000;
    align-self: flex-start;
    position: relative;
}
.friendName {
    font-style: normal;
    font-weight: 600;
    font-size: 18px;
    line-height: 22px;
    color: #000;
    text-align: left;
}

.friendMessageText {
    font-family: 'Montserrat';
    white-space: normal;
}

.friendTime {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 600;
    font-size: 10px;
    line-height: 12px;
    color: #000000;
    text-align: center;
    margin-top: 5px;
}
